<template>
  <div class="box" ref="box">
      <canvas class="box" ref="canvas"></canvas>
  </div>
</template>
<script>
export default {
  name: "home",
  data() {
    return {
      totalTime:0
    };
  },
  created() {},
  mounted() {
    console.log(this.$refs.canvas.offsetWidth);
    var cont = this.$refs.canvas;
    //  计算画布的宽度
    var width = cont.offsetWidth,
    //  计算画布的高度
    height = cont.offsetHeight;
    var cxt=cont.getContext("2d");
    console.log(width)
    cont.width = width;
    cont.height = height;
    cxt.fillText('前大苏打',50,50)
    // var w = ($(window).width() + 200) / 2;
    // var h = $(window).height() / 2;
    // var cont = $('#cont');
    // var totalTime = 0;

    // $('#cont').css('width', $(window).width());
    // $('body').css({ background: color() });
    // cont.style.background = this.color();

    var word = '前,方,高,能,不要眨眼,!,! !,! ! !,';
    word += '学,习,前端,已经,很,久,久久,久久久,久久久久久久,!!!,';
    word += '下,面,注意,了,下面,做,一,个,快闪,效果,快,闪,快,快快,快快快,';
    word = word.split(',');
    for (var i = 0; i < word.length; i++) {
        this.myAnimate(cxt,word[i], this.rm(200) + 300);
    }
    
  },
  methods: {
    rm(num) {
      return Number.parseInt(Math.random() * num);
    },
    color() {
      return "rgb(" + this.rm(256) + "," + this.rm(256) + "," + this.rm(256) + ")";
    },
    myAnimate(_paint,text, time) {
      var that = this;
      this.totalTime += time;
      setTimeout(function() {
        // that.$refs.box.style.background = that.color();
        // cont.html(text).css({
        //   //left: rm(w),
        //   //top: rm(h),
        //   //color:'rgb('+rm(256)+','+rm(256)+','+rm(256)+'),'
        //   fontSize: this.rm(200) + 100
        // });
        that.canvasText(_paint, text, "100px", "#fff", 300);
      }, this.totalTime);
    },
    canvasText(_paint,_text,_fontSzie,_color,_height){
        var that = this;
        _paint.font = _fontSzie;
        _paint.fillStyle = _color;
        _paint.textAlign = "center";
        _paint.textBaseline = "middle";
         _paint.clearRect(0,0,that.$refs.canvas.width,that.$refs.canvas.height);  
        _paint.fillText(_text, that.$refs.canvas.width/2, that.$refs.canvas.height/2);
    }
  }
};
</script>
<style lang="scss" scoped>
.box {
  width: 100%;
  height: 100%;
  background-color: #000;
  color: #fff;
}
</style>
