  <div class="box" ref="box" >
    <div class="section" ref="main">
        <span class="fadeInDown"  :style="{'animation-delay':([index]*0.2)+'s'}" v-for="(item,index) in text" :key="Date.parse(new Date())">{{item}}</span>
    </div>
  </div>
</template>
<script>
export default {
  name: "home",
  data() {
    return {
      totalTime:0,
      text:'',
    };
  },
  created() {},
  mounted() {
    var word = [
      {
        text:'前,方,高,能',
        page:4,
        time:0,
        style:"h"
      },
      {
        text:'这,不,是,P,P,T',
        page:6,
        time:800,
        style:"h"
      },{
        text:'这,是',
        page:2,
        time:1200,
        style:"h"
      },{
        text:'我,对',
        page:2,
        time:400,
        style:"h"
      },{
        text:'杨,倩',
        page:2,
        time:400,
        style:"h"
      },{
        text:'爱,的,宣,言',
        page:4,
        time:800,
        style:"h"
      },{
        text:'我,爱,你',
        page:3,
        time:2000,
        style:"h"
      }
    ]
    // word = word.split(',');
    for (var i = 0; i < word.length; i++) {
        this.myAnimate(word[i], this.rm(200) + 300);
    }
  },
  methods: {
    rm(num) {
      return Number.parseInt(Math.random() * num);
    },
    color() {
      return "rgb(" + this.rm(256) + "," + this.rm(256) + "," + this.rm(256) + ")";
    },
    myAnimate(text) {
      var that = this;
     this.totalTime += text.time;
     console.log(this.totalTime)
     
      setTimeout(function() {
         console.log(that.totalTime)
       
        if(text.page>1){
          that.text = (text.text).split(',');
          console.log(that.text)
        }else{
           that.text = text.text;
        }
       
        
       that.$refs.box.style.fontSize =  50 +'px';
      }, that.totalTime);
    
    }
  }
};
</script>
